@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700;800&display=swap);
/* font-family: 'Red Hat Display', sans-serif; */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.bodyContainer {
  background: url(/07_orderSummaryComponentMain/static/media/pattern-background-desktop.5a0ab689.svg);
  background-color: hsl(225, 100%, 94%);
  background-repeat: no-repeat;
  background-size: contain;
  align-items: center;
  display: flex;
  height: 100vh;
  justify-content: center;
  width: 100%;
}
.outerContainer {
  background-color: white;
  border-radius: 15px;
  height: 40em;
  width: 26em;
}

@media (max-width: 376px) {
  .bodyContainer {
    background: url(/07_orderSummaryComponentMain/static/media/pattern-background-mobile.c8d50fcb.svg);
    background-color: hsl(225, 100%, 94%);
    background-repeat: no-repeat;
    background-size: contain;
  }
  .outerContainer {
    height: 38em;
    width: 21em;
  }
}

/* font-family: 'Red Hat Display', sans-serif; */
.detailSection {
  display: flex;
  flex-direction: column;
  font-family: "Red Hat Display", sans-serif;
  padding: 2.5em 2em 1em 2em;
}
.detailSection > * {
  justify-content: center;
  text-align: center;
}
.detailSection h1 {
  margin: 0 0 0.5em 0;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.detailSection p {
  color: hsl(224, 23%, 55%);
  line-height: 1.4em;
  margin: 0 0 1.5em 0;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.btn {
  background-color: hsl(245, 75%, 52%);
  border: none;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgb(196, 196, 196);
  color: white;
  cursor: pointer;
  font-weight: 700;
  margin: 0 0 2em 0;
  outline: none;
  padding: 1em 0;
}
.btn:hover {
  background-color: hsl(244, 80%, 64%);
}
.cancelLink {
  color: hsl(224, 23%, 55%);
  font-size: 15px;
  font-weight: 700;
  text-decoration: none;
}
.cancelLink:hover {
  color: hsl(223, 47%, 23%);
}

/* font-family: 'Red Hat Display', sans-serif; */
.planCard {
  align-items: center;
  background-color: hsl(225, 100%, 98%);
  border-radius: 15px;
  display: flex;
  flex-direction: row;
  font-family: "Red Hat Display", sans-serif;
  margin: 0 0 1.5em 0;
  padding: 0.3em 1em;
}
.left {
  align-items: center;
  display: flex;
  flex-direction: row;
  flex: 4 1;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.musicIcon {
  background: url(/07_orderSummaryComponentMain/static/media/icon-music.f20c7787.svg);
  height: 3em;
  margin: 0 1em 0 0;
  width: 3em;
}
.planName {
  padding: 1.5em 0 0 0;
}
.left p {
  color: hsl(224, 23%, 55%);
  font-size: 16px;
}
.right {
  display: flex;
  flex: 1 1;
}
.planChangeLink {
  color: hsl(245, 75%, 52%);
  font-size: 14px;
  font-weight: 700;
}
.planChangeLink:hover {
  color: hsl(244, 80%, 64%);
  text-decoration: none;
}

.heroSection {
  border-radius: 10px 10px 0 0;
  background: url(/07_orderSummaryComponentMain/static/media/illustration-hero.73032583.svg);
  background-size: cover;
  background-repeat: no-repeat;
  height: 13em;
  width: 26em;
}

@media (max-width: 376px) {
  .heroSection {
    height: 11em;
    width: 21em;
  }
}

